.dropzone {
    height: 150px;
    width: 150px;
    background-color: #fff;
    border: 2px dashed rgb(187, 186, 186);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 16px;
}

.dropzone .file-input {
    display: none;
}

.highlight {
    background-color: rgb(188, 185, 236);
}